<template>
    <v-app>
        <v-container grid-list-xl>
          <v-layout justify-center row wrap>
            <v-flex xs12 md7 lg5 xl4 :class="{'mt-4': $vuetify.breakpoint.smAndDown, 'mt-5 pt-5': $vuetify.breakpoint.mdAndUp}">
            <h1 class="text-xs-center">Let's get in touch</h1>
            <v-card class="mt-3 contact-card" :class="{'pa-4': $vuetify.breakpoint.smAndDown, 'pa-5': $vuetify.breakpoint.mdAndUp}">
              <v-form
                    action="https://formspree.io/ivanmaurovic7@gmail.com"
                    method="POST"
                    ref="form"
                    v-model="valid"
                    lazy-validation
                >
                    <input type="hidden" name="_next" value="/" />
                    <v-text-field
                    name="name"
                    label="Name"
                    prepend-icon="person"
                    required
                    ></v-text-field>

                    <v-text-field
                    name="_replyto"
                    :rules="emailRules"
                    label="E-mail"
                    prepend-icon="email"
                    required
                    ></v-text-field>

                    <v-textarea
                    name="message"
                    :rules="messageRules"
                    label="Message"
                    prepend-icon="message"
                    required
                    ></v-textarea>

                    <v-btn
                    type="submit"
                    @click="validate"
                    :disabled="!valid"
                    class="orange accent-4 white--text mt-4 mx-auto d-block"
                    >
                    Send
                    </v-btn>

              </v-form>
            </v-card>
              <v-flex xs4 md1 d-flex>
                <a href="skype:ivanmaurovic7?chat" class="img-link img-link1 ma-1"><img src="./../assets/skype.png"></a>
                <a href="https://github.com/ivanmaurovic7" class="img-link img-link2 ma-1"><img src="./../assets/github.png"></a>
                <a href="https://hr.linkedin.com/in/ivan-maurovic-7a292a17b" class="img-link img-link3 ma-1"><img src="./../assets/linkedin.png"></a>
                <a href="mailto:ivanmaurovic7@gmail.com" class="img-link img-link4 ma-1"><img src="./../assets/mail.png"></a>
            </v-flex>
            </v-flex>
          </v-layout>
           
      </v-container>
    </v-app>
</template>


<script>
  export default {
    data: () => ({
      valid: true,
      name: '',
      message: '',
      email: '',
      emailRules: [
        v => !!v || 'This field is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid'
      ],
      messageRules: [
          v => !!v || 'This field is required'
      ]
    }),

    methods: {
      validate() {
        if(!this.$refs.form.validate()) {
            return
        }
      }
    }
  }
</script>

<style lang="sass">

.contact-card
    background-color: rgba(255, 255, 255, 0.7) !important

.img-link
  img
    display: block
    margin: 0 auto
    transition: transform .2s ease-in-out
    &:hover
      transform: scale(1.1) !important

.img-link1
    animation: slide-up .6s forwards 1.5s ease-in-out
    opacity: 0

.img-link2
    animation: slide-up .6s forwards 1.7s ease-in-out
    opacity: 0

.img-link3
    animation: slide-up .6s forwards 1.9s ease-in-out
    opacity: 0

.img-link4
    animation: slide-up .6s forwards 2.1s ease-in-out
    opacity: 0

@keyframes slide-up 
  0% 
    transform: translateY(50px)
    opacity: 0

  100% 
    transform: translateY(0px)
    opacity: 1

</style>
